import Tabs from "../components/Tabs";
import { useState } from "react";
import Outgoing from "../features/Outgoing/Views/Outgoing";
import Incoming from "./Incoming";
import Corporate from "./Corporate";
import Citizen from "./Citizen";
import Others from "./Others";

const MainComponent = () => {
    const [activeTab, setActiveTab] = useState(1);
    const handleSelectTab = (index) => {
        setActiveTab(index);
    }
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-12 text-center">
                        <img src="https://cdn.jsdelivr.net/gh/wagar1/Search/VugarJSLibrary/socar_logo.png" alt="Logo" id="socar_logo" height="40px" />
                        <span style={{fontWeight: 'bold', fontSize: '30px'}}>SOCAR sorular</span>               
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <Tabs selectTab={handleSelectTab} active={activeTab}/>
                </div>
            </div>
            <div className="row mt-2">
                <div className="col-12">
                    {
                        activeTab == 1 ? <Incoming /> :
                        activeTab == 2 ? <Outgoing /> :
                        activeTab == 3 ? <Corporate /> :
                        activeTab == 4 ? <Citizen /> : 
                        activeTab == 5 ? <Others /> : <></>
                    }         
                </div>
            </div>
        </div>
    );
}

export default MainComponent;