const MultipleSelectArea = (props) => {
    const id = props.id;
    return (
        <div className="input-group input-group-sm mb-2">
            <div className="input-group-prepend">
                <span className="input-group-text">{props.name}</span>
            </div>
            <div>
                <select className="column_filter form-control" 
                        aria-label="Small" 
                        aria-describedby="inputGroup-sizing-sm"
                        onChange={props.onSelectChanged}
                        defaultValue="none"
                >
                   <option value="none" disabled>Se</option>
                    {
                        props.options && props.options.map(x => <option key={x.id} value={x.value}>{x.name}</option>)
                    }
                </select>
            </div>
            <div className="input-group-append">
                <button className="btn btn-secondary" onClick={props.addNewInput}><i className="bi bi-plus"></i></button>
                {
                    props.hasDelete ? <button className="btn btn-light" onClick={() => props.deleteInput(id)}><i className="bi bi-dash"></i></button>
                                    : <></>
                }
            </div>     
        </div>
    )
}

export default MultipleSelectArea;