import './Components.css';

const SelectArea = (props) => {
    return (
        <div className="input-group input-group-sm mb-2">
                <div className="input-group-prepend">
                    <span className="input-group-text">{props.name}</span>
                </div>
                <div>
                    <select className="column_filter form-control" 
                            aria-label="Small" 
                            aria-describedby="inputGroup-sizing-sm" 
                            onChange={props.onSelectChanged}
                            defaultValue={props.defaultValue}
                    >
                        <option value="none" disabled>Se</option>
                        {
                            props.options && props.options.map(x => <option key={x.id} value={x.value}>{x.name}</option>)
                        }
                    </select>
                </div>
        </div>
    )
}

export default SelectArea;