import axios from 'axios';

const getDocuments = async () => {
  const result = await axios.get('https://ecm.socar.az/otcs/llisapi.dll/api/v1/nodes/180383760/output?format=json', {
      headers: {
          'OTCSTicket': window.OTCSTicket
      }
  });
  return result.data;
};

export default getDocuments;
