const Tabs = (props) => {
    const handleSelectTab = (index) => {
        props.selectTab(index);
    }
    return (
        <ul className="nav nav-tabs">
              <li className="nav-item">
                <a className={`nav-link ${props.active == 1 ? 'active': ''}`} href="#" onClick={()=>handleSelectTab(1)}>Daxil olan</a>
              </li>
              <li className="nav-item">
                <a className={`nav-link ${props.active == 2 ? 'active': ''}`} href="#" onClick={()=>handleSelectTab(2)}>Gndriln</a>
              </li>
              <li className="nav-item">
                <a className={`nav-link ${props.active == 3 ? 'active': ''}`} href="#" onClick={()=>handleSelectTab(3)}>Korporativ</a>
              </li>
              <li className="nav-item">
                <a className={`nav-link ${props.active == 4 ? 'active': ''}`} href="#" onClick={()=>handleSelectTab(4)}>Vtnda mracitlri</a>
              </li>
              <li className="nav-item">
                <a className={`nav-link ${props.active == 5 ? 'active': ''}`} href="#" onClick={()=>handleSelectTab(5)}>Digr</a>
              </li>
        </ul>
    );
}

export default Tabs;