import { useState } from "react";
import './Views.css';
import MyDatePicker from "../components/MyDatePicker";
import InputArea from "../components/InputArea";
const defaultLists = [
    { id: 1, name: 'Bir mssisy rhbr gln btn sndlr', active: false },
    { id: 2, name: 'Blokladrlan istifadilr hesabat', active: false },
    { id: 3, name: 'Daxil ediln tarix intervalnda seiln istifadinin yaratd sndlr', active: false },
    { id: 4, name: 'Count of Outgoing and incoming documents', active: false }
];

const First = () => {
    return (
        <>
            <MyDatePicker name={"Balanc tarix"} customc="othersfirst" />
            <MyDatePicker name={" Son tarix"} customc="othersfirst" />
            <InputArea name="Sndi gndrn mssis" /> 
            <InputArea name="Sndi qbul edn mssis" /> 
            <button className="btn btn-secondary" style={{marginLeft: '250px'}} >Ntic</button>
        </>
    )
}

const Others = () => {
    const [lists, setLists] = useState(defaultLists);
    const handleSelect = (id) => {
        let dLists = [...lists];
        dLists = dLists.map(x=> { x.active = false; return x;});
        const ind = dLists.findIndex(x=>x.id === id);
        dLists[ind].active = true;
        setLists(dLists);
    }
    return (
        <div className="container-fluid">
            <div className="row">
                <div className="col-3">
                    <ul className="list-group">
                        {
                            lists.map(x => <li className={ `listel list-group-item ${x.active ? 'active': ''}`} key={x.id} onClick={() => handleSelect(x.id)}>{x.name}</li>)
                        }
                    </ul>
                </div>
                <div className="col-9">
                    {
                        lists[0].active == true ? <First /> : <></>
                    }
                </div>
            </div>
        </div>
    );
}

export default Others;