import create from "zustand";
import { devtools } from 'zustand/middleware';
import {v4 as uuid} from 'uuid';

const store = (set, get) => ({
    defaultYear: '2022',
    years: [
        {
            id: uuid(), value: '2014', name: '2014'
        },
        {
            id: uuid(), value: '2015', name: '2015'
        },
        {
            id: uuid(), value: '2016', name: '2016'
        },
        {
            id: uuid(), value: '2017', name: '2017'
        },
        {
            id: uuid(), value: '2018', name: '2018'
        },
        {
            id: uuid(), value: '2019', name: '2019'
        },
        {
            id: uuid(), value: '2020', name: '2020'
        },
        {
            id: uuid(), value: '2021', name: '2021'
        },
        {
            id: uuid(), value: '2022', name: '2022'
        }
    ],
    defaultType: 'Tsdiqlnmi',
    types: [
        {
            id: uuid(), value: 'Tsdiqlnmi', name: 'Tsdiqlnmi'
        },
        {
            id: uuid(), value: 'Tsdiqlnmmi', name: 'Tsdiqlnmmi'
        }
    ],
    disapprovedStatuses: [
        {
            id: uuid(), value: 'Created', name: 'Created'
        },
        {
            id: uuid(), value: 'In reviewing', name: 'In reviewing'
        },
        {
            id: uuid(), value: 'In approval', name: 'In approval'
        },
        {
            id: uuid(), value: 'In correction', name: 'In approval'
        },
        {
            id: uuid(), value: 'Rejected', name: 'Rejected'
        },
        {
            id: uuid(), value: 'Reviewed', name: 'Reviewed'
        },
        {
            id: uuid(), value: 'Terminated', name: 'Terminated'
        },
        {
            id: uuid(), value: 'In registration', name: 'In registration'
        }
    ],
    approvedStatuses: [
        {
            id: uuid(), value: 'Attach Copy&Print', name: 'Attach Copy&Print'
        },
        {
            id: uuid(), value: 'Registered', name: 'Registered'
        },
        {
            id: uuid(), value: 'Send out', name: 'Send out'
        }
    ]
});

const useOutgoing = create(devtools(store));

export default useOutgoing;