import { useState } from "react";
import getDocuments from "../../../hooks/getDocuments";
import useOutgoing from "../../../store/useOutgoing";
import shallow from 'zustand/shallow';
import OutgoingComponents from "./OutgoingComponents";

const getState = state => [
    state.years, state.types,
    state.defaultYear, state.defaultType,
    state.disapprovedStatuses, state.approvedStatuses
]
const Outgoing = () => {
    const [documents, setDocuments] = useState([]);
    const [wait, setWait] = useState(false);
    const [years, types, defaultYear, defaultType,
        disapprovedStatuses, approvedStatuses] = useOutgoing(getState, shallow);
    const [statusList, setStatusList] = useState(approvedStatuses); 
    const handleGetResult = async () => {
        setWait(true);
        const tmp = (await getDocuments()).data;
        const docs = JSON.parse(tmp).data;
        docs.pop();
        setDocuments(docs);
        setWait(false);
    }
    const handleChangeInitiator = (event) => {
         window.gridTable.columns(6).search(event.target.value).draw();   
    }
    const handleChangeSenderOrganization = (event) => {
        window.gridTable.columns(0).search(event.target.value).draw();   
    } 
    const handleSelectTypeChange = (event) => {
        const selectedType = event.target.value;
        let statuses = [];
        if(selectedType === 'Tsdiqlnmi'){
            statuses = [...approvedStatuses];
        }else if(selectedType === 'Tsdiqlnmmi'){
            statuses = [...disapprovedStatuses];
        }
        setStatusList(statuses);
    }
    const handleStatusChange = (event) => {
        console.log(console.log(event.target.value));
    }
    return (
        <OutgoingComponents 
            defaultYear = { defaultYear }
            years = { years } 
            defaultType = { defaultType }
            types = { types }
            wait = { wait } 
            documents = { documents } 
            statusList = { statusList }
            onStatusChange = { handleStatusChange }
            onGetResult = {handleGetResult}
            onChangeSenderOrganization = { handleChangeSenderOrganization }
            onChangeInitiator = { handleChangeInitiator } 
            onSelectTypeChanged = { handleSelectTypeChange }
        />
    );
}

export default Outgoing;