import DatePicker from "react-datepicker";
import { useState } from "react";
import "react-datepicker/dist/react-datepicker.css";
import './Components.css';

const MyDatePicker = (props) => {
  const [startDate, setStartDate] = useState(new Date());
  return (
    <div className="input-group input-group-sm mb-2">
        <div className="input-group-prepend">
            <span className={`input-group-text ${props.customc ? props.customc : ''}`}>{props.name}</span>
        </div>
        <DatePicker className="column_filter form-control" aria-label="Small" aria-describedby="inputGroup-sizing-sm" selected={startDate} onChange={(date) => setStartDate(date)} />
    </div>
  );
}

export default MyDatePicker;