import { useState } from "react";
import { v4 as uuidv4 } from 'uuid';
import MultipleInputArea from "./MultipleInputArea";
import MultipleSelectArea from "./MultipleSelectArea";

const MultipleArea = (props) => {
    const [inputs, setInputs] = useState([{id: uuidv4()}]);
    const hasDelete = inputs.length > 1 ? true : false;
    const handleAddNewInput = () => {
        let nInputs = [...inputs];
        nInputs.push({id: uuidv4()});
        setInputs(nInputs);
    }
    const handleDeleteInput = (id) => {
        if(inputs.length === 1) return;
        let nInputs = [...inputs];
        const elId = nInputs.findIndex(x=>x.id === id);
        nInputs.splice(elId, 1);
        setInputs(nInputs);
    }
    return (
        <div>
            {
                inputs.map((x, i)=> { 
                    var el = props.isInput ? <MultipleInputArea name={props.name + ' ' + (i+1)} id={x.id} key={x.id} hasDelete={hasDelete} addNewInput={handleAddNewInput} deleteInput={handleDeleteInput} />
                    : <MultipleSelectArea name={props.name + ' ' + (i+1)} 
                                options={props.options} 
                                onSelectChanged= { props.onSelectChanged }
                                defaultValue = { props.defaultValue } 
                                id={x.id} key={x.id} hasDelete={hasDelete}
                                addNewInput={handleAddNewInput} deleteInput={handleDeleteInput} />
                    return el;
                })
            }
        </div>
    )
}

export default MultipleArea;