import MyDatePicker from "../../../components/MyDatePicker";
import InputArea from "../../../components/InputArea";
import MultipleArea from "../../../components/MultipleArea";
import SelectArea from '../../../components/SelectArea';
import Datatable from "../Components/Datatable";

const OutgoingComponents = (props) => {
    return (
        <div className="container-fluid">
                <div className="row">
                    <div className="col-5">
                        <InputArea name="Hans idardn" change={props.onChangeSenderOrganization} />
                        <MultipleArea name="Hans idary" isInput={true} />                                      
                    </div>
                    <div className="col-5">
                        <InputArea name="Tbbskar" change={props.onChangeInitiator} /> 
                        <MultipleArea name="Gndrn" isInput={true} />                                 
                    </div>
                    <div className="col-2"></div>
                </div>
                <hr></hr>
                <div className="row">
                    <div className="col-5">
                        <SelectArea name="l" defaultValue = {props.defaultYear} options={props.years} />
                        <MyDatePicker name={"Hans tarixdn"} />
                        <MyDatePicker name={"Hans tarix"} />
                    </div>
                    <div className="col-5">
                        <SelectArea name="Qsa mzmun" />
                        <SelectArea name="Tip" defaultValue = {props.defaultType} options={props.types} onSelectChanged={props.onSelectTypeChanged} />             
                        <MultipleArea 
                            name = "Status" isInput = {false}
                            options = {props.statusList}
                            onSelectChanged = { props.onStatusChange }
                            defaultValue = { props.defaultStatus }
                        />  
                    </div>
                    <div className="col-2">
                        <button className="btn btn-secondary pull-bottom" onClick={props.onGetResult}>Ntic</button>
                    </div>
                </div>
                <div className="app row mt-3">
                    {
                        props.wait ? 'Loading....' : <></>
                    }
                    <Datatable data={props.documents}></Datatable>
                </div>
        </div>
    );
}

export default OutgoingComponents;